.App {
  margin-top: 5vh;
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  font-family: "ars-maquette-web", sans-serif;
  text-rendering: optimizeLegibility;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

